import React from "react";
import styled from 'styled-components';
const Subscription = () => {
  return (
    <section>
      <SocialText>Follow me on <a href="https://twitter.com/aguidehub" rel="noopener" target="_BLANK">Twitter</a></SocialText>
      <SubscriptionContainer>
        <a href="https://portfolio.aguidehub.com/" target={"_blank"} ><img src="/portfolio-website.webp" height="731" width="681" loading="lazy" alt="Portfolio Website" /></a> 
      </SubscriptionContainer>
    </section>
  );
};

export default Subscription;

const SubscriptionContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  border: 2px solid #6166DC;
  background: #fff;
  box-shadow: 0px 0px 10px rgb(0 0 0 / 65%);
  z-index: 1;
`;

const SocialText = styled.h3`
  margin-bottom: 20px;
  text-align: center;
`;